.hero {
    .bg-intro {
        position: relative;
        text-align: center;
        outline: 2px solid;
        padding: 10em 0 10em 0;
        background-image: url("../images/bg-intro-mobile.svg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        z-index: -2;
        .hero-img {
            height: 70vh;
            position: absolute;
            top: -38%;
            right: 0;
            z-index: -5;
        }
    }
    @media (min-width: 675px) {
        width: 100%;
    }
}

.logo {
    height: 15vh;
    z-index: 100000;
}
